<script>
 export default {
   data(){
     return {
       position: {}
     }
   },
    methods: {
      onCancel() {
        alert('onCancel');
      },
      showSelectPosition(){
        this.$popup(mapSelectPosition, {
          hasSearch: true,
          allowDrag: true,
          centerTip: '',
          params: {
            lat: 28.228259,
            lng: 112.938904
          }
        }).then(position => {
          this.position = position;
        });
      },

      filter (type, options) {
        if (type == 'days') {
          return options.filter(v => v % 2 == 0)
        }
        return options
      },

      format (type, value) {
        if (type == 'year') {
          return `${value}`
        } 
        else if (type == 'month') {
          return `${value}`
        }
        return value
      }
    },
  }
</script>


